  getCategoryText(category) {
    const categoryMap = {
      general: 'ÿπÿßŸÖ',
      specialized: 'ŸÖÿ™ÿÆÿµÿµ',
      emergency: 'ÿ∑Ÿàÿßÿ±ÿ¶',
    };
    return categoryMap[category] || category;
  }

  formatDate(date) {
    if (!date) return 'ÿ∫Ÿäÿ± ŸÖÿ≠ÿØÿØ';
    return new Date(date).toLocaleDateString('ar-SA');
  }

  formatDateTime(dateTime) {
    if (!dateTime) return 'ÿ∫Ÿäÿ± ŸÖÿ≠ÿØÿØ';
    return new Date(dateTime).toLocaleString('ar-SA');
  }

  saveToLocalStorage() {
    try {
      localStorage.setItem('advancedMedicalReports', JSON.stringify(this.reports));
      localStorage.setItem('advancedTemplates', JSON.stringify(this.templates));
      localStorage.setItem('advancedGenerated', JSON.stringify(this.generated));
      localStorage.setItem('advancedApprovals', JSON.stringify(this.approvals));
      localStorage.setItem('advancedVersions', JSON.stringify(this.versions));
      localStorage.setItem('advancedAnalytics', JSON.stringify(this.analytics));
    } catch (error) {
      console.error('Error saving to localStorage:', error);
    }
  }

  loadFromLocalStorage() {
    try {
      this.reports = JSON.parse(localStorage.getItem('advancedMedicalReports') || '[]');
      this.templates = JSON.parse(localStorage.getItem('advancedTemplates') || '[]');
      this.generated = JSON.parse(localStorage.getItem('advancedGenerated') || '[]');
      this.approvals = JSON.parse(localStorage.getItem('advancedApprovals') || '[]');
      this.versions = JSON.parse(localStorage.getItem('advancedVersions') || '[]');
      this.analytics = JSON.parse(localStorage.getItem('advancedAnalytics') || '[]');
    } catch (error) {
      console.error('Error loading from localStorage:', error);
    }
  }

  setupEventListeners() {
    this.createReport = this.createReport.bind(this);
    this.createTemplate = this.createTemplate.bind(this);
    this.switchView = this.switchView.bind(this);
    this.handleFilterChange = this.handleFilterChange.bind(this);
    this.handleSearch = this.handleSearch.bind(this);
    this.viewReport = this.viewReport.bind(this);
    this.generateReport = this.generateReport.bind(this);
    this.submitForApproval = this.submitForApproval.bind(this);
    this.editReport = this.editReport.bind(this);
    this.useTemplate = this.useTemplate.bind(this);
    this.editTemplate = this.editTemplate.bind(this);
    this.viewGenerated = this.viewGenerated.bind(this);
    this.downloadGenerated = this.downloadGenerated.bind(this);
    this.approveReport = this.approveReport.bind(this);
    this.rejectReport = this.rejectReport.bind(this);
    this.viewVersion = this.viewVersion.bind(this);
    this.restoreVersion = this.restoreVersion.bind(this);
  }

